@import "../../global.scss";

.testimonial{
    background-color:#fff;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    @include  mobile {
        justify-content: space-around;
    }

    h1{
        font-size: 50px;
        @include  mobile {
            font-size:20px;
        }
    }

    .container{
        width:100%;
        height:80%;
        display: flex;
        justify-content: center;
        align-items: center;

        @include  mobile {
            flex-direction: column;
            height:100%;
        }
       
    }
    .card{
        width:320px;
        height:60%;
        border-radius: 10px;
        box-shadow: 0px 0px 15px -8px black;
        display: flex;
        justify-content: space-between;
        flex-direction: column;
        padding:20px;
        margin:0 30px;
        transition: all 1s ease;
        @include  mobile {
             width:200px;
            height:250px;
            margin:10px 0;
        }

        &.featured{
            width:350px;
            height:75%;
            margin:0 30px;

            @include  mobile {
                width:250px;
                height:200px;
                margin:2px; 
                
            }
        }
        &:hover{
            transform: scale(1.1);
        }

        .top{
            display: flex;
            justify-content: center;
            align-items: center;

            img{
                &.left,&.right{
                    height:25px;
                }
                &.user{
                    width:60px;
                    height: 60px;
                    border-radius: 50%;
                    object-fit: cover;
                    margin:0px 30px;
                    @include  mobile {
                        width:30px;
                        height:30px;
                    }
                }

            }
        }
        .center{
            padding:10px;
            border-radius: 10px;
            background-color: rgb(228, 225, 224);

            p{
                font-size: 16px;
                @include  mobile {
                    font-size: 12px;
                }
            }
            @include  mobile {
                font-size: 14px;
                padding:5px;
            }
        }
        .bottom{
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;

            h3{
                margin-bottom: 5px;

                @include  mobile {
                    font-size: 12px;
                }
            }
            h4{
                color:grey;
                @include  mobile {
                    font-size: 10px;
                }
            }
        }

    }
}