@import "../../global.scss";

.topbar{
    width:100%;
    height:70px;
    background-color: rgb(215, 100, 250);
    color:$maincolor;
    position: fixed;
    top:0;
    z-index:3;
    transition: all 1s ease;

    .wrapper{
        padding:10px 30px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .left{
            display: flex;
            align-items: center;

            .logo{
                font-size: 40px;
                font-weight: 700;
                text-decoration: none;
                color: inherit;
                margin-right:40px;

                @include mobile{
                    font-size: 30px;
                }
            }
            .itemContainer{
                display: flex;
                align-items: center;
                margin-left: 30px;

                @include mobile{
                    display: none;
                }

                .icon{
                    font-size: 22px;
                    margin-right:5px;
                }
                .span{
                    font-size: 22px;
                    font-weight: 400;
                }
            }
        }

        .right{

            .humburger{
                width:32px;
                height:25px;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                cursor:pointer;

                span{
                    width:100%;
                    height:3px;
                    background-color: $maincolor;
                    transform-origin:left ;
                    transition: all 2s ease;

                }
            }
        }
    }
&.active{
    background-color: $maincolor;
    color:white;


    .humburger{
    span{
        &:first-child{
            background-color: white;
            transform: rotate(45deg);
        }
        &:nth-child(2){
            opacity: 0;
        }
        &:last-child{
            background-color: white;
            transform: rotate(-45deg);
        }
    }
    }
}

}